import React, { Fragment, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import ProgressBar from "@ramonak/react-progress-bar";
import styles from "./LocalServerMatrics.module.css";
import { toast, ToastContainer } from "react-toastify";

// import {
//   onFetchihngLocalService,
// } from "../../store/localService/LocalService";

const LocalServerMetrics = () => {
  // const dispatch = useDispatch();
  const { token } = useSelector((state) => state.login);

  const { services, loadingServices } = useSelector(
    (state) => state.localServices
  );

  const [local, setLocal] = useState("");
  const [loading, setLoading] = useState(false);
  // const [totalStorage, setTotalStorage] = useState("");

  const [free_Storage, setFree_Storage] = useState("");
  const [total_Storage, setTotal_Storage] = useState("");
  const [cPU_Usage, setCPU_Usage] = useState("");
  const [free_Memory, setFree_Memory] = useState("");
  const [total_Memory, setTotal_Memory] = useState("");
  const [connections, setConnections] = useState("");
  const [max_Connections , setMax_Connections] = useState("")

  // const Free_Storage = local.Free_Storage.split(' ')[0];
  // const Total_Storage = local.Total_Storage.split(' ')[0];
  // const CPU_Usage = local.CPU_Usage.split(' ')[0];
  // const Free_Memory = local.Free_Memory.split(' ')[0];
  // const Total_Memory = local.Total_Memory.split(' ')[0];

  const handleUptateUsers = () => {
    fetch(`http://${localStorage.getItem("ipAddress")}:3001/api/users/updateUsers`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((res) => {
        return res.json();
      })
      .then((resData) => {
        console.log("38", resData);
        if (resData.error) {
          toast(resData.error);
        }
        if (resData.error && resData.error[0]) {
          toast(resData.error[0].message);
        }
        if (resData.message) {
          toast.success(resData.message);
        }
      });
  };

  useEffect(() => {
    fetch(`http://${localStorage.getItem("ipAddress")}:3001/localServerMetrics`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((res) => res.json())
      .then((resData) => {
        console.log(resData);
        setLocal(resData);
        setFree_Storage(resData.Free_Storage.split(' ')[0])
        setTotal_Storage(resData.Total_Storage.split(' ')[0])
        setCPU_Usage(resData.CPU_Usage.split(' ')[0])
        setFree_Memory(resData.Free_Memory.split(' ')[0])
        setTotal_Memory(resData.Total_Memory.split(' ')[0])
        setConnections(resData.Connections)
        setMax_Connections(resData.Max_Connections)

        
        setLoading(true);
      });
  }, []);

  // console.log("hhhhhhhhhhhhhhh", local);
  // console.log("ffffffffffffff", local.Total_Storage);

  // setTotalStorage(parseFloat(local.Total_Storage))
  // console.log("======", parseFloat(local.Total_Storage));

  // const num = "72.22222333333333 GB";
  // const test = num.split(' ')[0];
  // console.log(test)
  // const str = parseFloat((Math.round(num * 100) / 100).toFixed());
  // console.log("jjjjjjjjjjjjjjjjjjjjjjj", str);

  // const Free_Storage = free_Storage;
  // const Total_Storage = total_Storage;
  // const CPU_Usage = local.CPU_Usage.split(' ')[0];
  // const Free_Memory = local.Free_Memory.split(' ')[0];
  // const Total_Memory = local.Total_Memory.split(' ')[0];

  return (
    <Fragment>
      <div className="container">
        <ToastContainer />
        <h4 className="text-center my-4" style={{ color: "#232E58" }}>
          LOCAL SERVER METRICS
        </h4>
        <div className="row">
          <div className="col-12 m-auto my-2">
            <div className="row">
              <div className="col-6 d-flex justify-content-start">
                <p className="my-0" style={{ fontSize: "14px" }}>
                  AVAILABLE STORAGE
                </p>
              </div>
              <div className="col-6 d-flex justify-content-end">
                <p className="text-center my-0" style={{ fontSize: "14px" }}>
                  {(Math.round(free_Storage * 100) / 100).toFixed(2)} GB FREE
                  OF {(Math.round(total_Storage * 100) / 100).toFixed(2)} GB
                </p>
              </div>
            </div>
            {loadingServices ? (
              <div style={{ textAlign: "center" }}>
                <div className="spinner-border" role="status">
                  <span className="sr-only">Loading...</span>
                </div>
              </div>
            ) : (
              <ProgressBar
                completed={ 
                  parseFloat(
                  (Math.round(free_Storage * 100) / 100).toFixed(0)
                )}
                maxCompleted={ 
                  parseFloat(
                  (Math.round(total_Storage * 100) / 100).toFixed(0)
                )}
                className={styles.wrapper}
                barContainerClassName={styles.container}
                labelClassName={styles.label}
                bgColor={"#196282"}
                height={"35px"}
                labelColor={"#000"}
              />
            )}
          </div>
        </div>

        <div className="row">
          <div className="col-12 m-auto my-2">
            <div className="row">
              <div className="col-6 d-flex justify-content-start">
                <p className="my-0" style={{ fontSize: "14px" }}>
                  CPU USAGE
                </p>
              </div>
              <div className="col-6 d-flex justify-content-end">
                <p className="text-center my-0" style={{ fontSize: "14px" }}>
                  {(Math.round(cPU_Usage * 100) / 100).toFixed(2)}% OF 100%
                </p>
              </div>
            </div>
            <ProgressBar
              completed={parseFloat(
                (Math.round(cPU_Usage * 100) / 100).toFixed()
              )}
              className={styles.wrapper}
              barContainerClassName={styles.container}
              labelClassName={styles.label}
              bgColor={"#2BB6D0"}
              height={"35px"}
              labelColor={"#000"}
            />
          </div>
        </div>

        <div className="row">
          <div className="col-12 m-auto my-2">
            <div className="row">
              <div className="col-6 d-flex justify-content-start">
                <p className="my-0" style={{ fontSize: "14px" }}>
                  AVAILABLE RAM
                </p>
              </div>
              <div className="col-6 d-flex justify-content-end">
                <p className="text-center my-0" style={{ fontSize: "14px" }}>
                  {(Math.round(free_Memory * 100) / 100).toFixed(2)} MB FREE 
                  OF {(Math.round(total_Memory * 100) / 100).toFixed(2)} MB
                </p>
              </div>
            </div>
            <ProgressBar
              completed={parseFloat(
                (Math.round(free_Memory * 100) / 100).toFixed()
              )}
              maxCompleted={parseFloat(
                (Math.round(total_Memory * 100) / 100).toFixed()
              )}
              className={styles.wrapper}
              barContainerClassName={styles.container}
              labelClassName={styles.label}
              bgColor={"#196282"}
              height={"35px"}
              labelColor={"#000"}
            />
          </div>
        </div>

        <div className="row">
          <div className="col-12 m-auto my-2">
            <div className="row">
              <div className="col-6 d-flex justify-content-start">
                <p className="my-0" style={{ fontSize: "14px" }}>
                  CONNECTED CLIENTS
                </p>
              </div>
              <div className="col-6 d-flex justify-content-end">
                <p className="text-center my-0" style={{ fontSize: "14px" }}>
                  {(Math.round(connections * 100) / 100).toFixed(2)} OF
                  {(Math.round(max_Connections * 100) / 100).toFixed(2)}
                </p>
              </div>
            </div>
            <ProgressBar
              completed={parseFloat(
                (Math.round(connections * 100) / 100).toFixed()
              )}
              maxCompleted={parseFloat(
                (Math.round(max_Connections * 100) / 100).toFixed()
              )}
              className={styles.wrapper}
              barContainerClassName={styles.container}
              labelClassName={styles.label}
              bgColor={"#2BB6D0"}
              height={"35px"}
              labelColor={"#000"}
            />
          </div>
        </div>

        <div className="row my-4">
          <div className="col-6 col-md-4 w-100 d-flex justify-content-center">
            <button
              className="btn btn-primary"
              onClick={() => handleUptateUsers()}
            >
              update users
            </button>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
export default LocalServerMetrics;
