import React, { Fragment, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";
import {
  onDeletingProject,
  onFetchingProjects,
} from "../../store/Projects/ProjectsReducer";

export default function Projects() {
  const dispatch = useDispatch();
  const { id } = useParams();
  const token = useSelector((state) => state.login.token);
  const { projects, loadFetchingProjects } = useSelector(
    (state) => state.projects
  );

  const [globalIdentifier, setGlobalIdenetifier] = useState(null);

  useEffect(() => {
    dispatch(onFetchingProjects(id, token));

    fetch(
      `${process.env.REACT_APP_URL}/api/globalIdentifiers/${id}/projects?mode=main`,
      {
        headers: { Authorization: `Bearer ${token}` },
      }
    )
      .then((res) => res.json())
      .then((resData) => {
        console.log(resData);
        setGlobalIdenetifier(resData.globalIdentifier);
        // dispatch(onFinishFetchingProjects(resData.projects));
        // dispatch(onFinishFetchingGlobalIdentifiers(resData.globalIdentifier));
      });
  }, []);

  const styleLinkBack = {
    textDecoration: "none",
    color: "#717993",
    fontSize: "22px",
  };

  return (
    <Fragment>
      <div className="container">
        {globalIdentifier?.name ? (
          <Link
            to={"/globalidenetifiers/projects/" + globalIdentifier.gid}
            style={styleLinkBack}
          >
            {globalIdentifier.name}
          </Link>
        ) : null}

        {/* <Link to={"/gid"} style={styleLinkBack}>
            GID 1
        </Link>
        <span className="mx-2" style={{ color: "#28345C" }}>
          <i class="fas fa-chevron-right"></i>
          <i class="fas fa-chevron-right"></i>
        </span>
        <Link to={"/gid"} style={styleLinkBack}>
          Project 1
        </Link> */}
        <div className="row">
          <div className="col-12 col-md-6  m-auto">
            <h3 className="text-center mt-2">Projects</h3>
          </div>
        </div>

        {loadFetchingProjects ? (
          <div style={{ textAlign: "center" }}>
            <div className="spinner-border" role="status">
              <span className="sr-only">Loading...</span>
            </div>
          </div>
        ) : projects && projects.length > 0 ? (
          <div className="row mt-3">
            {projects.map((project) => {
              return (
                <div className="col-10 col-md-5 m-auto my-2" key={project.id}>
                  <div className="border py-1">
                    <div className="col-6 d-flex justify-content-center">
                      {/* <div className="row">
                      <div className="col-8 col-md-7 d-flex justify-content-end">
                        <Link style={{textDecoration:"none" , color:"black"}} to={"/locations/" + project.id}>
                            {project.name}
                        </Link>
                      </div>
                      <div className="col-4 col-md-5 d-flex justify-content-end ">
                          <i className="fa fa-download text-secondary  mx-2 mt-1"></i>    
                      </div> */}
                      <Link
                        style={{ textDecoration: "none", color: "black" }}
                        to={"/locations/" + project.id}
                      >
                        {project.name}
                      </Link>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        ) : (
          <div style={{ textAlign: "center" }}>No Projects Added Yet.</div>
        )}
      </div>
    </Fragment>
  );
}
