import logo from "./logo.svg";
import "./App.css";
import { Routes, Route, Navigate } from "react-router-dom";

import LoginPage from "./pages/login/LoginPage";
import GlobalIdenetifiersPage from "./pages/global idenetifiers/GlobalIdenetifiersPage";
import { authCheckState, onChagingIpAddress } from "./store/Login/Login";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import ProjectsPage from "./pages/project/ProjectsPage";
import LocalServerMetricsPage from "./pages/LocalServerMetrics/LocalServerMetricsPage";
import CreateLocInfoPage from "./pages/locs/CreateLocInfoPage";
// import GidPage from "./pages/gid/GidPage";
import ProjectPage from "./pages/project/ProjectPage";
import GlobalIdenetifiersLocalPage from "./pages/global idenetifiers/GlobalIdenetifiersLocalPage";
import ProjectsLocalPage from "./pages/project/ProjectsLocalPage";
import ProjectLocalPage from "./pages/project/ProjectLocalPage";
import CreateSingleLocInfoPage from "./pages/locs/CreateSingleLocInfoPage";
import CreateDualLocInfoPage from "./pages/locs/CreateDualLocInfoPage";
import ViewLocsPage from "./pages/locs/ViewLocsPage";
import CreateDualLocInfoLocalPage from "./pages/locs/CreateDualLocInfoLocalPage";
import CreateSingleLocInfoLocalPage from "./pages/locs/CreateSingleLocInfoLocalPage";
import ViewLocsLocalPage from "./pages/locs/ViewLocsLocalPage";

function App(props) {
  const dispatch = useDispatch();
  const token = useSelector((state) => state.login.token);
  const ipAddress = localStorage.getItem("ipAddress");
  useEffect(() => {
    dispatch(authCheckState());

    dispatch(onChagingIpAddress(ipAddress));
  }, [dispatch, token]);

  if (!token) {
    return (
      <Routes>
        <Route path="/login" element={<LoginPage />} />
        <Route path="/" element={<LoginPage />} />
        {/* <Route path="*" element={<Navigate to="/login" />} /> */}
      </Routes>
    );
  }

  return (
    <div>
      <Routes>
        <Route exact path="/" element={<LocalServerMetricsPage />} />
        <Route exact path="/login" element={<LoginPage />} />
        {/* <Route exact path="/gid" element={<GidPage/>} /> */}

        {/* main */}
        <Route
          exact
          path="/globalidenetifiers"
          element={<GlobalIdenetifiersPage />}
        />
        <Route
          exact
          path="/globalidenetifiers/projects/:id"
          element={<ProjectsPage />}
        />
        <Route exact path="/locations/:id" element={<ProjectPage />} />

        {/* Local */}
        <Route
          exact
          path="/globalidenetifierslocal/projectslocal/:id"
          element={<ProjectsLocalPage />}
        />
        <Route
          exact
          path="/globalidenetifierslocal"
          element={<GlobalIdenetifiersLocalPage />}
        />
        <Route
          exact
          path="/locationslocal/:id"
          element={<ProjectLocalPage />}
        />

        <Route
          exact
          path="/LocalServerMetrics"
          element={<LocalServerMetricsPage />}
        />
        <Route exact path="/CreateLocInfo" element={<CreateLocInfoPage />} />

        {/* main */}
        <Route exact path="/viewlocs/:id" element={<ViewLocsPage />} />
        <Route
          exact
          path="/CreateSingleLocInfo/:id"
          element={<CreateSingleLocInfoPage />}
        />
        <Route
          exact
          path="/CreateDualLocInfo/:id"
          element={<CreateDualLocInfoPage />}
        />

        {/* Local */}
        <Route
          exact
          path="/viewlocsLocal/:id"
          element={<ViewLocsLocalPage />}
        />
        <Route
          exact
          path="/CreateSingleLocInfoLocal/:id"
          element={<CreateSingleLocInfoLocalPage />}
        />
        <Route
          exact
          path="/CreateDualLocInfoLocal/:id"
          element={<CreateDualLocInfoLocalPage />}
        />
      </Routes>
    </div>
  );
}

export default App;
