import { updateObject } from "../../util/utility";

const START_FETCHING_LOACL_SERVICE =
  "KELTECH/STORE/LOCALSERVICE/START_FETCHING_LOCAL_SERVICE";
const FINISH_FETCHING_LOCAL_SERVICE =
  "KELTECH/STORE/LOCALSERVICE/FINISH_FETCHING_LOCAL_SERVICE";

const initialState = {
  services: {},
  loadingServices: false,
};

export const onStartFetchingSevices = () => {
  return { type: START_FETCHING_LOACL_SERVICE };
};

const startFetchingLocalService = (state, action) => {
  return updateObject(state, { loadingServices: true });
};

export const onFetchihngLocalService = (token) => {
  return (dispatch) => {
    dispatch(onStartFetchingSevices());
    fetch(`http://${localStorage.getItem("ipAddress")}:3001/localServerMetrics`, {
      headers: { Authorization: `Bearer ${token}` },
    })
      .then((res) => {
        if (res.status !== 200 && res.status !== 201) {
          // throw new Error("something went worng!!");
          console.log("error");
        }
        return res.json();
      })
      .then((resData) => {
        console.log("34", resData);
        dispatch(onFinishFetchingLocalService(resData.services));
      });
  };
};

export const onFinishFetchingLocalService = (localServices) => {
  return { type: FINISH_FETCHING_LOCAL_SERVICE, localServices };
};

const finishFetchingLocalServices = (state, action) => {
  return updateObject(state, { loadingServices: false ,services:action.loadingServices});
};
export default function LocalServicesReducer(state = initialState, action) {
  switch (action.type) {
    case START_FETCHING_LOACL_SERVICE:
      return startFetchingLocalService(state, action);
    case FINISH_FETCHING_LOCAL_SERVICE:
      return finishFetchingLocalServices(state, action);

    default:
      return state;
  }
}

